/* ==========калькулятор=============== */
.calculator {
  width: 160px !important;
  height: 235px !important;
  border-radius: 4px 4px 4px 4px;
}

.calculator .operator-keys {

  border-radius: 0 0 4px 0;
}

.calculator-display {
  font-size: 3em !important;
  line-height: 65px !important;
  border-radius: 4px 4px 0 0;
  
}
.calculator-display,.digit-keys  {
  cursor: move;
  /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}

/* (Optional) Apply a "closed-hand" cursor during drag operation. */
.calculator-display:active,
.digit-keys:active {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.calculator-keypad {
  height: 175px !important;
  border-radius: 0 0 4px 4px;
}

.calculator-keypad .input-keys {
  width: 120px !important;
}

.calculator-keypad .digit-keys {
  border-radius: 0 0 0 4px;
}

.calculator-key {
  width: 40px !important;
  height: 35px !important;
  line-height: 35px !important;
  font-size: 1em !important;
}

.calculator-key:last-child {
  padding-top: 0 !important;
}

.calculator .digit-keys .key-0 {
  padding-left: 0 !important;
  text-align: center !important;
}

/* =========Caalendar style======================= */

.rdp-head {
  color: #52555f;
}
.rdp-day_selected{
background-color:#ff751d;
}

:root {
  --rdp-cell-size: 40px;
  --rdp-accent-color: #ff751d !important;
  --rdp-outline: 2px solid var(--rdp-accent-color) !important;
  --rdp-background-color: #f7bf9db1 !important;

}
